<template>
  <div class="results">
    <h2></h2>
    <p class="mission">MISSION COMPLETE</p>
    <p>: {{ round }}/5</p>
    <p>: {{ score }}</p>

    <!--   -->
    <div class="leaderboard-input">
      <input type="text" v-model="playerName" placeholder="Your Name" />
      <button @click="submitScore" :disabled="!playerName || submitted"></button>
    </div>

    <!--   -->
    <div v-if="wrongAnswers.length" class="wrong-section">
      <h3></h3>
      <div
        v-for="(item, index) in wrongAnswers"
        :key="item.id"
        class="wrong-item"
      >
        <p class="question-title"> {{ index + 1 }}</p>
        <p class="question-content">{{ item.content }}</p>
        <p class="question-explanation">{{ item.explanation }}</p>
      </div>
    </div>

    <!--  -->
    <div v-else class="perfect-text"> </div>

    <!--   -->
    <h3></h3>
    <ol class="leaderboard">
      <li v-for="(entry, index) in leaderboard" :key="index">
        <span class="rank">{{ index + 1 }}.</span>
        <span class="name">{{ entry.name }}</span>
        <span class="score">{{ entry.score }} </span>
      </li>
    </ol>

    <!--   -->
    <button class="restart" @click="$emit('restart')"></button>
  </div>

  <!--   -->
  <div class="hacker-grid"></div>
</template>

<script>
import axios from "axios";
//  scam  real scamMessages 
import { scamMessages } from "../database";
import { realMessages } from "../database_true"; // 

export default {
  name: "GameResults",
  props: {
    round: Number,
    score: Number,
    wrongIds: { //  ID  ( number  string)
      type: Array,
      default: () => []
    }
  },
  emits: ["restart"],
  data() {
    return {
      playerName: "",
      leaderboard: [],
      submitted: false,
      wrongAnswers: []
    };
  },
  methods: {
    async fetchLeaderboard() {
      try {
        const res = await axios.get("http://localhost:3000/leaderboard");
        this.leaderboard = res.data;
      } catch (err) {
        console.error("", err);
      }
    },
    async submitScore() {
      if (!this.playerName) return;
      try {
        const res = await axios.post("http://localhost:3000/leaderboard", {
          name: this.playerName,
          score: this.score
        });
        if (res.data.success) {
          this.leaderboard = res.data.leaderboard;
          this.submitted = true;
        }
      } catch (err) {
        console.error("", err);
      }
    },
    loadWrongQuestions() {
      console.log('GameResults: wrongIds:', this.wrongIds);
      
      // 
      const scamArray = Array.isArray(scamMessages) ? scamMessages : [];
      console.log('GameResults: :', scamArray.length);
      
      //  wrongIds  msg.id  number/string mismatch
      const wrongIdStrs = this.wrongIds.map((id) => String(id));
      this.wrongAnswers = scamArray.filter((msg) => wrongIdStrs.includes(String(msg.id)));

      console.log('GameResults: ID:', wrongIdStrs);
      console.log('GameResults: :', this.wrongAnswers.length);
      console.log('GameResults: :', this.wrongAnswers);
    }
  },
  mounted() {
    this.fetchLeaderboard();
    this.loadWrongQuestions();
  },
  watch: {
    wrongIds: {
      handler() {
        console.log('GameResults: wrongIds prop ');
        this.loadWrongQuestions();
      },
      immediate: true
    }
  }
};
</script>

